.container {
  max-width: 1140px;
  margin: auto;
}

.heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #27272b;
  box-shadow: 0px 0px 12px #18191b;
  border-radius: 0.5rem;
  margin: 2rem 1rem;
  padding: .7rem 1rem;
  font-weight: 700;
}

.coin-name {
  margin-left: -4rem;
}

@media screen and (max-width:720px) {
  .hide-mobile {
    display: none;
  }
}